html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

.content-box-component {
  box-sizing: content-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-style: normal;
  color: #788891; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden; }

.header {
  position: absolute;
  width: 100%; }

.header__content {
  margin: 0 auto;
  max-width: 1448px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 31px 7px 31px 30px; }

.header__contacts-list {
  margin: 0;
  padding: 0; }

.logo {
  display: flex; }

.logo__wrapper {
  width: 52px;
  height: 52px; }

.logo__title {
  margin: 0;
  margin-left: 16px;
  padding-top: 6px;
  position: relative;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  font-style: normal;
  color: #ffffff; }

.logo__subtitle {
  position: absolute;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  font-style: normal;
  color: #788891; }

.btn {
  display: inline-block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid;
  border-radius: 5px; }
  .btn:hover, .btn:focus {
    opacity: 0.6; }
  .btn:active {
    opacity: 0.3; }

.contacts-list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  list-style: none; }

.contacts-list__item--phone {
  margin-left: 122px; }

.contacts-list__item--order {
